'use strict';

/**
* Create an iterator which rounds each iterated value to the nearest power of two toward positive infinity.
*
* @module @stdlib/math/iter/special/ceil2
*
* @example
* var uniform = require( '@stdlib/random/iter/uniform' );
* var iterCeil2 = require( '@stdlib/math/iter/special/ceil2' );
*
* var iter = iterCeil2( uniform( -100.0, 100.0 ) );
*
* var r = iter.next().value;
* // returns <number>
*
* r = iter.next().value;
* // returns <number>
*
* r = iter.next().value;
* // returns <number>
*
* // ...
*/

// MODULES //
var iterator = __var_0__;


// EXPORTS //
export default iterator;
